export default function resizeImageOffScreen(imageUrl) {
  return new Promise((resolve, reject) => {
    fetch(imageUrl)
      .then((response) => response.blob())
      .then((blob) => createImageBitmap(blob))
      .then((imageBitmap) => {
        const floor = Math.floor
        const offscreenCanvas = new OffscreenCanvas(imageBitmap.width, imageBitmap.height)
        let targetWidth, targetHeight

        let scaleFactor = floor(imageBitmap.width / (280 * 3))

        if (scaleFactor >= 2) {
          if (scaleFactor >= 5) scaleFactor = floor(scaleFactor / 1.3)
          if (scaleFactor % 2 !== 0) scaleFactor--

          targetWidth = imageBitmap.width / scaleFactor
          targetHeight = imageBitmap.height * (targetWidth / imageBitmap.width)

          if (targetHeight < 140) {
            targetWidth *= 140 / targetHeight
            targetHeight = 140
          }
        } else {
          targetWidth = imageBitmap.width
          targetHeight = imageBitmap.height
        }

        offscreenCanvas.width = targetWidth
        offscreenCanvas.height = targetHeight

        const context = offscreenCanvas.getContext('2d')
        context.drawImage(imageBitmap, 0, 0, targetWidth, targetHeight)

        offscreenCanvas
          .convertToBlob({ type: 'image/jpeg', quality: 0.8 })
          .then((resizedBlob) => {
            const reader = new FileReader()
            reader.onloadend = () => {
              resolve(reader.result)
            }
            reader.readAsDataURL(resizedBlob)
          })
          .catch(() => {
            reject(null)
          })
      })
      .catch(() => {
        reject(null)
      })
  })
}
