import orderBy from 'lodash/orderBy'

const DatabaseService = {
  async dropTables() {
    const { installedOn } = await browser.storage.local.get({
      installedOn: null
    })

    await browser.storage.local.clear()

    if (installedOn) {
      await browser.storage.local.set({ installedOn })
    }
  },

  async getGroups(withHomeGroup = true, order = 'manual') {
    const result = await browser.storage.local.get({
      groups: [],
      preferences: {}
    })

    let groups = result.groups
    const savedPreferences = result.preferences

    if (withHomeGroup) {
      groups = [
        {
          id: 0,
          title: savedPreferences?.defaultGroupName || browser.i18n.getMessage('home'),
          position: 0
        },
        ...groups
      ]
    }

    switch (order) {
      case 'alphabetical':
        return groups.sort((a, b) => (a.title || '').toLowerCase().localeCompare((b.title || '').toLowerCase()))
      default:
        return orderBy(groups, 'position', 'asc')
    }
  },

  async getBookmarks() {
    const result = await browser.storage.local.get({
      bookmarks: []
    })

    return result.bookmarks
  },

  async getBookmarksByGroup(id, order = 'manual') {
    let orderAttribute
    let orderSort

    switch (order) {
      case 'visits':
        orderAttribute = ['visits', 'title']
        orderSort = ['desc', 'asc']
        break
      case 'date_desc':
        orderAttribute = ['ts_created']
        orderSort = ['desc']
        break
      case 'date_asc':
        orderAttribute = ['ts_created']
        orderSort = ['asc']
        break
      case 'alphabetical':
        orderAttribute = [({ title }) => (title || '').toLowerCase(), 'ts_created']
        orderSort = ['asc', 'asc']
        break
      default:
        orderAttribute = ['position', 'ts_created']
        orderSort = ['asc', 'asc']
    }

    const result = await browser.storage.local.get({
      bookmarks: []
    })

    const bookmarks = result.bookmarks.filter((bookmark) => Number(bookmark?.idgroup) === Number(id))

    // Add bookmark dataThumbnail
    for (const bookmark of bookmarks) {
      if (bookmark.thumbnail) {
        continue
      }

      bookmark.dataThumbnail = (
        await browser.storage.local.get({
          [`thumbnail-${bookmark.id}`]: null
        })
      )[`thumbnail-${bookmark.id}`]
    }

    return orderBy(bookmarks, orderAttribute, orderSort)
  },

  async getBookmarksCount() {
    const result = await browser.storage.local.get({
      bookmarks: []
    })

    return result.bookmarks.length
  },

  async search(text) {
    const searchQuery = (text || '').toLowerCase()
    const result = await browser.storage.local.get({
      bookmarks: [],
      groups: []
    })

    // TODO: Add matchsorter plugin and limit to 25 results
    const bookmarks = result.bookmarks
      .filter(
        ({ title, url }) =>
          (title || '').toLowerCase().indexOf(searchQuery) > -1 || (url || '').toLowerCase().indexOf(searchQuery) > -1
      )
      .slice(0, 24)
      .map((bookmark) => {
        const group = result.groups.find(({ id }) => Number(id) === Number(bookmark.idgroup)) || {}

        return {
          ...bookmark,
          groupTitle: group?.title || 'Home'
        }
      })

    // Add bookmark dataThumbnail
    for (const bookmark of bookmarks) {
      if (bookmark.thumbnail) {
        continue
      }

      bookmark.dataThumbnail = (
        await browser.storage.local.get({
          [`thumbnail-${bookmark.id}`]: null
        })
      )[`thumbnail-${bookmark.id}`]
    }

    return bookmarks
  },

  async getBookmarksWithNoThumbnail() {
    const storageKeys = Object.keys(await browser.storage.local.get(null))
    const thumbnailIds = storageKeys
      .filter((key) => key.startsWith('thumbnail-'))
      .map((key) => key.replace('thumbnail-', ''))
      .map((id) => Number(id))

    const result = await browser.storage.local.get({
      bookmarks: []
    })

    return result.bookmarks.filter(({ id, thumbnail }) => {
      return !thumbnail && !thumbnailIds.includes(Number(id))
    })
  },

  async getBookmarksWithThumbnail() {
    const result = await browser.storage.local.get({
      bookmarks: []
    })

    return result.bookmarks.filter(({ thumbnail }) => !thumbnail)
  },

  async createBookmark(bookmark) {
    if (!bookmark['title'] || !bookmark['url']) {
      return
    }

    const result = await browser.storage.local.get({
      bookmarks: []
    })

    const autoIncrement = !result.bookmarks.length ? 1 : Math.max(...result.bookmarks.map(({ id }) => id)) + 1

    const record = {
      id: autoIncrement,
      title: bookmark['title'],
      url: bookmark['url'],
      thumbnail: bookmark['thumbnail'] || '',
      position: typeof bookmark['position'] === 'undefined' ? 999 : Number(bookmark['position']),
      idgroup: Number(bookmark['idgroup']) || 0,
      visits: 0,
      visits_morning: 0,
      visits_afternoon: 0,
      visits_evening: 0,
      visits_night: 0,
      ts_created: Math.round(new Date().getTime() / 1000)
    }

    await browser.storage.local.set({
      bookmarks: [...result.bookmarks, record]
    })

    return record
  },

  async createBookmarkWithId(bookmark) {
    const result = await browser.storage.local.get({
      bookmarks: []
    })

    // Prevent from creating duplicate bookmarks
    if (result.bookmarks.find(({ id }) => Number(id) === Number(bookmark['id']))) {
      return
    }

    const record = {
      id: Number(bookmark['id']),
      title: bookmark['title'],
      url: bookmark['url'],
      thumbnail: bookmark['thumbnail'] || '',
      position: typeof bookmark['position'] === 'undefined' ? 999 : Number(bookmark['position']),
      idgroup: Number(bookmark['idgroup']) || 0,
      visits: Number(bookmark['visits']) || 0,
      visits_morning: Number(bookmark['visits_morning']) || 0,
      visits_afternoon: Number(bookmark['visits_afternoon']) || 0,
      visits_evening: Number(bookmark['visits_evening']) || 0,
      visits_night: Number(bookmark['visits_night']) || 0,
      ts_created: Number(bookmark['ts_created']) || Math.round(new Date().getTime() / 1000)
    }

    await browser.storage.local.set({
      bookmarks: [...result.bookmarks, record]
    })

    return record
  },

  async editBookmark(bookmark) {
    const result = await browser.storage.local.get({
      bookmarks: []
    })

    const bookmarks = result.bookmarks.map((item) => {
      return Number(item.id) !== Number(bookmark.id)
        ? item
        : {
            ...item,
            title: bookmark['title'],
            url: bookmark['url'],
            thumbnail: bookmark['thumbnail'] || '',
            idgroup: Number(bookmark['idgroup']) || 0,
            position: typeof bookmark['position'] === 'undefined' ? 999 : Number(bookmark['position'])
          }
    })

    await browser.storage.local.set({
      bookmarks
    })

    return bookmark
  },

  async destroyBookmark(bookmark) {
    const result = await browser.storage.local.get({
      bookmarks: []
    })

    // Delete bookmark
    await browser.storage.local.set({
      bookmarks: result.bookmarks.filter(({ id }) => Number(id) !== Number(bookmark.id))
    })

    await browser.storage.local.remove(`thumbnail-${bookmark.id}`)
  },

  async bulkDestroyBookmarks(ids) {
    if (!Array.isArray(ids)) {
      return
    }

    ids = ids.map((id) => Number(id))

    const result = await browser.storage.local.get({
      bookmarks: []
    })

    await browser.storage.local.set({
      bookmarks: result.bookmarks.filter(({ id }) => !ids.includes(Number(id)))
    })
  },

  async bulkMoveBookmarks(ids, group) {
    const result = await browser.storage.local.get({
      bookmarks: []
    })

    ids = ids.map((id) => Number(id))

    const bookmarks = result.bookmarks.map((item) => {
      return !ids.includes(Number(item.id))
        ? item
        : {
            ...item,
            idgroup: Number(group)
          }
    })

    await browser.storage.local.set({
      bookmarks
    })
  },

  async createGroup(group) {
    const result = await browser.storage.local.get({
      groups: []
    })

    const autoIncrement = !result.groups.length ? 1 : Math.max(...result.groups.map(({ id }) => Number(id))) + 1

    const record = {
      id: autoIncrement,
      title: group['title'],
      position: typeof group['position'] === 'undefined' ? 99 : Number(group['position']),
      color: ''
    }

    await browser.storage.local.set({
      groups: [...result.groups, record]
    })

    return record
  },

  async createGroupWithId(group) {
    if (Number(group['id']) == 0) {
      return
    }

    const result = await browser.storage.local.get({
      groups: []
    })

    // Prevent from creating duplicate groups
    if (result.groups.find(({ id }) => Number(id) === Number(group['id']))) {
      return
    }

    const record = {
      id: Number(group['id']),
      title: group['title'],
      position: typeof group['position'] === 'undefined' ? 99 : Number(group['position']),
      color: ''
    }

    await browser.storage.local.set({
      groups: [...result.groups, record]
    })

    return record
  },

  async editGroup(group) {
    const result = await browser.storage.local.get({
      groups: []
    })

    const groups = result.groups.map((item) => {
      return Number(item.id) != Number(group.id)
        ? item
        : {
            ...item,
            title: group['title'],
            position: typeof group['position'] === 'undefined' ? 99 : Number(group['position'])
          }
    })

    await browser.storage.local.set({
      groups
    })

    return group
  },

  async destroyGroup(group) {
    const result = await browser.storage.local.get({
      bookmarks: [],
      groups: []
    })

    // Delete group
    await browser.storage.local.set({
      groups: result.groups.filter(({ id }) => Number(id) !== Number(group.id))
    })

    // Delete group bookmarks
    await browser.storage.local.set({
      bookmarks: result.bookmarks.filter(({ idgroup }) => Number(idgroup) !== Number(group.id))
    })
  },

  async updateBookmarksPosition(data) {
    const result = await browser.storage.local.get({
      bookmarks: []
    })

    const bookmarks = result.bookmarks.map((item) => {
      const { id, index } = data.find(({ id }) => Number(id) === Number(item.id)) || {}
      return id
        ? {
            ...item,
            position: Number(index)
          }
        : item
    })

    await browser.storage.local.set({
      bookmarks
    })
  },

  async updateGroupsPosition(data) {
    const result = await browser.storage.local.get({
      groups: []
    })

    const groups = result.groups.map((item) => {
      const { id, index } = data.find(({ id }) => Number(id) === Number(item.id)) || {}
      return id
        ? {
            ...item,
            position: Number(index)
          }
        : item
    })

    await browser.storage.local.set({
      groups
    })
  },

  async addThumbnail({ id, url, image }) {
    await browser.storage.local.set({
      [`thumbnail-${id}`]: image
    })
  },

  async getThumbnail({ id }) {
    const dataThumbnailKey = `thumbnail-${id}`
    return (await browser.storage.local.get(dataThumbnailKey))?.[dataThumbnailKey]
  },

  async cleanUpThumbnails() {
    const storageKeys = Object.keys(await browser.storage.local.get(null))
    const thumbnailIds = storageKeys
      .filter((key) => key.startsWith('thumbnail-'))
      .map((key) => key.replace('thumbnail-', ''))
      .map((id) => Number(id))

    const result = await browser.storage.local.get({
      bookmarks: []
    })

    const bookmarkIds = result.bookmarks.map(({ id }) => Number(id))
    const extraThumbnailIds = thumbnailIds.filter((id) => !bookmarkIds.includes(id))

    for (const id of extraThumbnailIds) {
      await browser.storage.local.remove(`thumbnail-${id}`)
    }
  },

  async getDuplicateBookmarks() {
    const result = await browser.storage.local.get({
      bookmarks: []
    })

    const urlToId = {}
    const duplicateBookmarksIds = []

    result.bookmarks.forEach(({ id, url }) => {
      if (urlToId[url]) {
        duplicateBookmarksIds.push(id)
      } else {
        urlToId[url] = id
      }
    })

    return duplicateBookmarksIds
  },

  async getEmptyGroups() {
    const result = await browser.storage.local.get({
      groups: [],
      bookmarks: []
    })

    const groupsFromBookmarks = result.bookmarks.map(({ idgroup }) => Number(idgroup))
    const emptyGroups = result.groups.filter(({ id }) => !groupsFromBookmarks.includes(Number(id)))

    return emptyGroups
  },

  async trackVisit(id) {
    const result = await browser.storage.local.get({
      bookmarks: []
    })

    const hours = new Date().getHours()

    let attribute
    if (hours >= 0) {
      attribute = 'visits_night'
    }
    if (hours >= 6) {
      attribute = 'visits_morning'
    }
    if (hours >= 12) {
      attribute = 'visits_afternoon'
    }
    if (hours >= 18) {
      attribute = 'visits_evening'
    }

    try {
      const bookmarks = result.bookmarks.map((item) => {
        return Number(item.id) !== Number(id)
          ? item
          : {
              ...item,
              visits: (Number(item.visits) || 0) + 1,
              [attribute]: (Number(item[attribute]) || 0) + 1
            }
      })

      await browser.storage.local.set({
        bookmarks
      })
    } catch (e) {
      console.log(e)
    }
  }
}

export default DatabaseService
