// Services
import defaultPreferences from '@/constants/defaultPreferences'
import DataService from '@/services/DataService'
import DatabaseService from '@/services/LocalStorageDatabaseService'
import SyncService from '@/services/SyncService'
import resizeImageOffScreen from '@/utils/resizeImageOffScreen'

// ****************************
// Alarms
// ****************************

const sync = async () => {
  DataService.sync()
}

const backup = async () => {
  DataService.backup()
}

const handleAlarm = (alarm) => {
  if (alarm?.name === 'sync') {
    sync()
  }

  if (alarm?.name === 'backup') {
    backup()
  }

  if (alarm?.name === 'contextMenu') {
    createContextMenu()
  }
}

// ****************************
// Install, Uninstall, Startup hooks
// ****************************

browser.runtime.onInstalled.addListener((details) => {
  if (details.reason === browser.runtime.OnInstalledReason.INSTALL) {
    browser.storage.local.set({ installedOn: Date.now() })
    browser.runtime.setUninstallURL('https://speeddial2.com/uninstall')
    browser.tabs.create({ url: 'override.html#/?welcome=true' })
  }

  // register alarms
  browser.alarms.create('sync', { periodInMinutes: 120 })
  browser.alarms.create('backup', { periodInMinutes: 1440 })
  browser.alarms.create('contextMenu', { periodInMinutes: 30 })
})

browser.runtime.onStartup.addListener(() => {
  // Synchronize
  sync()

  // Create context menu
  createContextMenu()
})

// ****************************
// Alarms
// ****************************

browser.alarms.onAlarm.removeListener(handleAlarm)
browser.alarms.onAlarm.addListener(handleAlarm)

// ****************************
// Context menu
// ****************************

browser.contextMenus.onClicked.addListener((info) => {
  if (info.menuItemId === 'root') {
    return
  }

  // Validate if the id is a number
  if (!info.menuItemId || isNaN(Number(info.menuItemId))) {
    return
  }

  saveCurrentTab(Number(info.menuItemId))
})

async function createContextMenu() {
  try {
    const { preferences } = await browser.storage.local.get({ preferences: defaultPreferences })

    if (!preferences.contextMenu) {
      return
    }

    const groups = await DatabaseService.getGroups()

    browser.contextMenus.removeAll().then(() => {
      browser.contextMenus.create({
        contexts: ['all'],
        title: 'Speed Dial 2',
        type: 'normal',
        id: 'root'
      })

      for (const group of groups) {
        browser.contextMenus.create({
          contexts: ['all'],
          title: groups.length > 1 ? `Save to ${group.title}` : 'Save current page',
          type: 'normal',
          id: `${group.id}`,
          parentId: 'root'
        })
      }
    })
  } catch (error) {
    // no-op
  }
}

const saveCurrentTab = async function (groupId = 0) {
  try {
    const tabs = await browser.tabs.query({ windowId: browser.windows.WINDOW_ID_CURRENT, active: true })

    if (!tabs || !tabs[0]) {
      throw new Error('No active tabs')
    }

    const { url, title } = tabs[0]

    if (!url || !title) {
      throw new Error('No url or title')
    }

    // Test if the url is valid
    new URL(url)

    // Validate the group exists
    const groups = await DatabaseService.getGroups()
    if (!groups.find((group) => Number(group.id) === Number(groupId))) {
      throw new Error('Selected group does not exist')
    }

    const result = await DatabaseService.createBookmark({ title: title, url: url, idgroup: groupId })

    // Sync
    SyncService.addBookmark(result)

    // Thumbnail
    const base64 = await browser.tabs.captureVisibleTab()
    const image = await resizeImageOffScreen(base64)

    await DatabaseService.addThumbnail({ id: result?.id, url, image })

    return true
  } catch {
    return false
  }
}
