// const syncUrl = 'http://localhost/speeddial2.com/sync2'
const syncUrl = 'https://www.speeddial2.com/sync2'

const client = (endpoint, { body, ...customConfig } = {}) => {
  const config = {
    method: body ? 'POST' : 'GET',
    ...customConfig
  }

  if (body) {
    config.body = body
  }

  return fetch(`${syncUrl}/${endpoint}`, config)
    .then((response) => response.json())
    .then((result) => {
      return result
    })
}

const SyncService = {
  async isAuthorized() {
    const result = await browser.storage.local.get({ preferences: {} })
    const { username, password } = result.preferences
    return typeof username !== undefined && typeof password !== undefined && username && password
  },

  async syncParams() {
    let params = new URLSearchParams()
    const result = await browser.storage.local.get({ preferences: {} })
    const { username, password } = result.preferences

    params.append('username', username)
    params.append('password', password)

    return params
  },

  async get() {
    if (!(await this.isAuthorized())) {
      return
    }

    let body = await this.syncParams()

    return client('get', { body })
  },

  async updatePreferences(data) {
    if (!(await this.isAuthorized())) {
      return
    }

    const preferences = { ...data }
    delete preferences.username
    delete preferences.password

    const payload = {
      preferences
    }

    let body = await this.syncParams()
    body.append('s', JSON.stringify(payload))

    return client('preferences', { body })
  },

  async backup(data) {
    if (!(await this.isAuthorized())) {
      return
    }

    let body = await this.syncParams()
    body.append('sync', JSON.stringify(data))

    return client('backup', { body })
  },

  async merge(data) {
    if (!(await this.isAuthorized())) {
      return
    }

    let body = await this.syncParams()
    body.append('sync', JSON.stringify(data))

    return client('merge', { body })
  },

  async import(data) {
    if (!(await this.isAuthorized())) {
      return
    }

    let body = await this.syncParams()
    body.append('sync', JSON.stringify(data))

    return client('import', { body })
  },

  async addBookmark(bookmark) {
    if (!(await this.isAuthorized())) {
      return
    }

    const payload = {
      action: 'create',
      id: bookmark.id,
      bookmark
    }

    let body = await this.syncParams()
    body.append('s', JSON.stringify(payload))

    return client('bookmark', { body })
  },

  async editBookmark(bookmark) {
    if (!(await this.isAuthorized())) {
      return
    }

    const payload = {
      action: 'edit',
      id: bookmark.id,
      bookmark
    }

    let body = await this.syncParams()
    body.append('s', JSON.stringify(payload))

    return client('bookmark', { body })
  },

  async destroyBookmark(bookmark) {
    if (!(await this.isAuthorized())) {
      return
    }

    const payload = {
      action: 'remove',
      id: bookmark.id
    }

    let body = await this.syncParams()
    body.append('s', JSON.stringify(payload))

    return client('bookmark', { body })
  },

  async bulkDestroyBookmarks(ids) {
    if (!(await this.isAuthorized())) {
      return
    }

    const payload = {
      action: 'bulk-remove',
      bookmarks: ids
    }

    let body = await this.syncParams()
    body.append('s', JSON.stringify(payload))

    return client('bookmark', { body })
  },

  async bulkMoveBookmarks(ids, group) {
    if (!(await this.isAuthorized())) {
      return
    }

    const payload = {
      action: 'bulk-move',
      bookmarks: ids,
      group: group
    }

    let body = await this.syncParams()
    body.append('s', JSON.stringify(payload))

    return client('bookmark', { body })
  },

  async reorderBookmarks(bookmarks) {
    if (!(await this.isAuthorized())) {
      return
    }

    let neworder = {}

    bookmarks.forEach(({ id, index }) => {
      neworder[id] = index
    })

    const payload = {
      action: 'reorder',
      bookmarks: neworder
    }

    let body = await this.syncParams()
    body.append('s', JSON.stringify(payload))

    return client('bookmark', { body })
  },

  async addGroup(group) {
    if (!(await this.isAuthorized())) {
      return
    }

    const payload = {
      action: 'create',
      id: group.id,
      group
    }

    let body = await this.syncParams()
    body.append('s', JSON.stringify(payload))

    return client('group', { body })
  },

  async editGroup(group) {
    if (!(await this.isAuthorized())) {
      return
    }

    const payload = {
      action: 'edit',
      id: group.id,
      group
    }

    let body = await this.syncParams()
    body.append('s', JSON.stringify(payload))

    return client('group', { body })
  },

  async getGroupSharing(group) {
    if (!(await this.isAuthorized())) {
      return
    }

    let body = await this.syncParams()
    body.append('id', group.id)

    return client('get_group_sharing', { body })
  },

  async setGroupSharing(group, isPublic, isEditable) {
    if (!(await this.isAuthorized())) {
      return
    }

    let body = await this.syncParams()
    body.append('id', group.id)
    body.append('is_public', Number(isPublic))
    body.append('is_editable', Number(isEditable) || 0)

    return client('set_group_sharing', { body })
  },

  async destroyGroup(group) {
    if (!(await this.isAuthorized())) {
      return
    }

    const payload = {
      action: 'remove',
      id: group.id
    }

    let body = await this.syncParams()
    body.append('s', JSON.stringify(payload))

    return client('group', { body })
  },

  async reorderGroups(groups) {
    if (!(await this.isAuthorized())) {
      return
    }

    let neworder = {}

    groups.forEach(({ id, index }) => {
      neworder[id] = index
    })

    const payload = {
      action: 'reorder',
      groups: neworder
    }

    let body = await this.syncParams()
    body.append('s', JSON.stringify(payload))

    return client('group', { body })
  },

  async getBackups() {
    if (!(await this.isAuthorized())) {
      return
    }

    let body = await this.syncParams()

    return client('get_backup', { body })
  },

  async getProfile() {
    if (!(await this.isAuthorized())) {
      return
    }

    let body = await this.syncParams()

    return client('test_account', { body })
  },

  signIn({ username, password }) {
    let body = new URLSearchParams()

    body.append('username', username)
    body.append('password', password)

    return client('test_account', { body })
  },

  signUp({ username, password }) {
    let body = new URLSearchParams()

    body.append('user[email]', username)
    body.append('user[password]', password)

    return client('sign_up', { body })
  },

  async upload(data) {
    if (!(await this.isAuthorized())) {
      return
    }

    let body = await this.syncParams()
    body.append('data', data)

    return client('upload', { body })
  }
}

export default SyncService
